@import url(https://fonts.googleapis.com/css2?family=Concert+One&family=Josefin+Sans&family=Pacifico&family=Varela+Round&display=swap);
*{
  font-family: 'Josefin Sans', sans-serif;
}
body {
  margin: 0;
  font-family: 'Josefin Sans', sans-serif;
 
}
a{
  list-style: none;
  list-style-type: none;
  text-decoration: none;
}

code {
  font-family: 'Josefin Sans', sans-serif;
}

.services{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #97bcf369;
    /* margin-top: 100px; */
    padding-top: 70px;
}
.cards{
    display: flex;
    flex-direction: row;
    width: 100vw;
    justify-content: space-around;

}
summary{
    font-size: larger;
    font-weight: bold;
    margin-left: 30px;
    margin-bottom: 20px;
    text-transform: capitalize;
    
   
}
summary:hover{
    color: chartreuse;
}
.first-cards{
    padding: 10px;
    padding-left: 30px;
}
.nxtbtn{
    margin-top: 70px;

}
.css-o69gx8-MuiCardMedia-root{
    
    object-fit: contain;
}


                           /* media quiries */
     @media only screen and (max-width: 800px) {
         .services{
             margin-top: 0;
             height: auto;
         }
         .cards{
             flex-direction: column;
             align-items: center;
         }
         .css-5nw623-MuiPaper-root-MuiCard-root{
                          margin: 10px;
         }
         .nxtbtn{
             display: none;
         }
        
     }
.pricepage{
    width: 100vw;
    height: 100vh;
    background-color: #8c52ff;
}
.heading{
    width: 100vw;
    height: 120px;
    text-align: center;
    text-transform: capitalize;
    padding-top: 75px;
}
.pricecards{
    display: flex;
    width: 100vw;
    justify-content: space-around;
}
.css-1hnbbzp-MuiTypography-root{
    padding: 0;
    padding-top: 40px;
    text-transform: capitalize;
}



               /* media quiries */
@media only screen and (max-width: 800px) {
    .pricepage{
        height: auto;
    }
    .pricecards{
        flex-direction: column;
        align-items: center;
    }
    .css-aan8o5-MuiPaper-root-MuiCard-root{
        width: 260px;
    }

}
.contactpage{
    width: 100vw;
    height: 100vh;
    background-color: black;
}
.contactheading{
    width: 100%;
    height: 10%;
    color: white;
    text-align: center;
    text-transform: capitalize;
    padding-top: 40px;
}
.contactgrid{
    width: 100vw;
    height: 90%;
    
    display: grid;
    grid-template-columns: 250px 250px;
    grid-template-rows: 100px 100px;
    justify-content: center;
    grid-column-gap:200px;
    grid-row-gap:50px;
    align-content: center;
}
.img{
    width: 30px;
    height: 30px;
}
.firstdiv,.seconddiv{
    display: flex;
    flex-direction: row;
    align-items: center;
    color: white;
}
.contacth5{
    margin: auto;
    margin-left: 6px;
}
.contacth5:hover{
    color: mediumblue;
}
                    



                                      /* media */
@media only screen and (max-width: 800px) {
    .contactheading{
        padding-top: 80px;
    }
    .contactgrid{
        grid-template-columns: 100vw;
        place-items: center;
        grid-template-rows: 125px 40px 102px 40px;
    }
}
.hirepage{
    width: 100vw;
    height: 100vh;
    text-align: center;
    background-color: cadetblue;
    
}
.hireheading h1{
    padding-top: 100px;
}
.hireone{
    margin-top: 30px;
}
.hireone img{
    width: 50px;
}
.hireone h4{
    padding-top: 10px;
}
.cvheading h3{
    
    margin: 40px;
    margin-top: 120px;
}
.cvpage{
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
a{
    color: black;
}
